@import url(https://fonts.googleapis.com/css?family=Lato:300,400);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
  background: #FEFEFE;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: inherit;
  color: #212121;
  font-weight: 100;
  font-family: 'Lato', sans-serif;
}

#root,
#root > div,
main {
  height: inherit;
}

.small .emojione {
  font-size: 22px;
}

.medium .emojione {
  font-size: 26px;
}

.large .emojione {
  font-size: 30px;
}

a {
  text-decoration: none;
  font-weight: bolder;
  color: #757575;
}

h1,
h2 {
  color: #757575;
  font-weight: bolder;
}

h1 {
  font-size: 32px;
  margin-bottom: 24px;
}

h2,
.page > section h1 {
  font-size: 24px;
  margin-bottom: 16px;
}

.docs {
  display: flex;
  height: calc(100% - 56px);
}

.page {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 24px;
  box-sizing: border-box;
}

@media (min-width: 768px) {
  .page {
    width: calc(100% - 200px);
  }
}

.page > section:not(:last-child) {
  margin-bottom: 24px;
}

.github {
  width: 24px;
}

.emojione {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  margin-top: -4px;
}

/**
* prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
* Based on https://github.com/chriskempson/tomorrow-theme
* @author Rose Pritchard
*/

code,
pre {
  color: #E46C75;
  background: none;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
       tab-size: 4;
  -webkit-hyphens: none;
      -ms-hyphens: none;
          hyphens: none;
  font-size: 14px;
  border-radius: 4px;
}

/* Code blocks */
pre {
  height: 100%;
  padding: 1em;
  margin: .5em 0;
  overflow: auto;
}

:not(pre) > code,
pre {
  background: #282C34;
}

code > *::-moz-selection,
pre > *::-moz-selection {
  background-color: #abb2bf;
}

code > *::selection,
pre > *::selection {
  background-color: #abb2bf;
}

/* Inline code */
:not(pre) > code {
  padding: .1em;
  border-radius: .3em;
  white-space: normal;
}

.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #999;
}

.token.punctuation {
  color: #ccc;
}

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #e2777a;
}

.token.tag {
  color: #E6C07C;
}

.token.attr-name {
  color: #D39858;
}

.token.function-name {
  color: #6196cc;
}

.token.boolean,
.token.number,
.token.function {
  color: #f08d49;
}

.token.function {
  color: #86b0f4;
}

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #f8c555;
}

.token.property {
  color: #ABB2BF;
}

.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #BF78DC;
}

.token.selector {
  color: #D39A67;
}

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #95C37A;
}

.token.operator,
.token.entity {
  color: #C978DC;
}

.token.url {
  color: #67cdcc;
}

.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.token.inserted {
  color: green;
}

